import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import AccordionActions from "@mui/material/AccordionActions";

const dispatchedEvents = [];

const SrsMuiAccordionActions = function (props, ref) {
  let componentProps = {...props, theme: props.muiTheme};
  return (
    <AccordionActions {...componentProps}>
      <slot />
    </AccordionActions>
  );
};

const styles = `
  /* Custom styles for the MuiAccordionActions component */
`;

const props = [
  {
    name: "disableSpacing",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to MuiAccordionActions if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiAccordionActions),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
